import * as XLSX from "xlsx";
import { Ebike, EbikeModel } from "./ebikeTypes";
import { QrCode } from "lucide-react";

// H√†m export ebikes ra file excel
export const exportEbikesToExcel = (ebikes: Ebike[], models: EbikeModel[]) => {
  const exportData = ebikes.map((e) => {
    const modelName = models.find((m) => m.id === e.modelId)?.name || "Unknown Model";

    return {
      modelName, // xu·∫•t ra model name thay v√¨ modelId
      serialNumber: e.serialNumber,
      vehicleID: e.vehicleID,
      plateNumber: e.plateNumber,
      odo: e.odo,
      color: e.color,
      status: e.status,
      currentLocation: e.currentLocation,
      lastMaintained: e.lastMaintained,
      batteryCapacity: e.batteryCapacity,
      range: e.range,
      pricePerDay: e.pricePerDay,
    };
  });

  const ws = XLSX.utils.json_to_sheet(exportData);
  const wb = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(wb, ws, "Ebikes");
  XLSX.writeFile(wb, "ebikes_export.xlsx");
};
