'use client';

import { useState, ChangeEvent } from 'react';
import { Ebike,EbikeStatus } from '@/src/lib/ebikes/ebikeTypes';
import { EbikeModel } from '@/src/lib/ebikes/ebikeModelTypes';
import { addDoc, collection, doc, getDocs, updateDoc, Timestamp } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';
import NotificationDialog from '@/src/components/ui/NotificationDialog';


type Props = {
  newEbike: Ebike;
  setNewEbike: (ebike: Ebike) => void;
  models: EbikeModel[];
  isUpdateMode: boolean;
  setIsUpdateMode: (v: boolean) => void;
  setEbikes: (ebikes: Ebike[]) => void;
  onSaveComplete: () => void;
  companyId: string; // ‚úÖ Th√™m d√≤ng n√†y
};

export default function EbikeForm({
  newEbike,
  setNewEbike,
  models,
  isUpdateMode,
  setIsUpdateMode,
  setEbikes,
  onSaveComplete,
  companyId, // ‚úÖ Nh·∫≠n t·ª´ props
}: Props) {
  const [dialog, setDialog] = useState({
    open: false,
    type: 'info' as 'success' | 'error' | 'info',
    title: '',
    description: '',
  });

  const show = (type: 'success' | 'error' | 'info', title: string, description = '') => {
    setDialog({ open: true, type, title, description });
  };

  const handleChange = <K extends keyof Ebike>(key: K, value: Ebike[K]) => {
    setNewEbike({
      ...newEbike,
      [key]: value,
    });
  };
  

  const handleDateChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (!value) return handleChange('lastMaintained', null);
    const [year, month, day] = value.split('-').map(Number);
    const date = new Date(Date.UTC(year, month - 1, day));
    handleChange('lastMaintained', Timestamp.fromDate(date));
  };

  const resetForm = () => {
    setNewEbike({
      id: '',
      modelId: '',
      serialNumber: '',
      vehicleID: '',
      plateNumber: '',
      odo: 0,
      color: '',
      status: 'Available',
      currentLocation: '',
      lastMaintained: Timestamp.fromDate(new Date()),
      batteryCapacity: 0,
      range: 0,
      pricePerDay: 0,
      companyId: '',
      stationId: '',
    });
    setIsUpdateMode(false);
  };

  const saveEbike = async () => {
    if (!newEbike.modelId) return show('error', 'Missing model', 'Please select an Ebike model.');

    const data: Partial<Ebike> = {
      ...newEbike,
      serialNumber: newEbike.serialNumber.trim(),
      vehicleID: newEbike.vehicleID.trim(),
      plateNumber: newEbike.plateNumber.trim(),
      currentLocation: newEbike.currentLocation.trim(),
      odo: Number(newEbike.odo) || 0,
      batteryCapacity: Number(newEbike.batteryCapacity) || 0,
      range: Number(newEbike.range) || 0,
      pricePerDay: Number(newEbike.pricePerDay) || 0,
    };

    try {
      if (isUpdateMode && newEbike.id) {
        await updateDoc(doc(db, 'ebikes', newEbike.id), data);
        show('success', 'Ebike updated');
      } else {
        await addDoc(collection(db, 'ebikes'), data);
        show('success', 'Ebike added');
      }

      resetForm();
      const snapshot = await getDocs(collection(db, 'ebikes'));
      const list = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() })) as Ebike[];
      setEbikes(list);
      onSaveComplete();
    } catch (err) {
      console.error(err);
      show('error', 'Failed to save ebike.');
    }
  };

  const ebikeStatusOptions: Ebike['status'][] = [
    'Available',
    'In Use',
    'Under Maintenance',
    'Reserved',
    'Broken',
  ];

  return (
    <>
      <div className="bg-white p-6 rounded-xl shadow mt-6">
        <h2 className="text-xl font-semibold mb-4">
          {isUpdateMode ? 'Update Ebike' : 'Add New Ebike'}
        </h2>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <select
            value={newEbike.modelId}
            onChange={(e) => handleChange('modelId', e.target.value)}
            className="border p-2 rounded"
          >
            <option value="">Select Model</option>
            {models.map((model) => (
              <option key={model.id} value={model.id}>
                {model.name}
              </option>
            ))}
          </select>

          <Input
            placeholder="Motor Serial Number"
            value={newEbike.serialNumber}
            onChange={(e) => handleChange('serialNumber', e.target.value)}
          />
          <Input
            placeholder="Vehicle ID"
            value={newEbike.vehicleID}
            onChange={(e) => handleChange('vehicleID', e.target.value)}
          />
          <Input
            placeholder="Plate Number"
            value={newEbike.plateNumber}
            onChange={(e) => handleChange('plateNumber', e.target.value)}
          />
          <Input
            type="number"
            placeholder="Odometer (km)"
            value={newEbike.odo || ''}
            onChange={(e) => handleChange('odo', e.target.valueAsNumber || 0)}
          />
          <Input
            placeholder="Color"
            value={newEbike.color}
            onChange={(e) => handleChange('color', e.target.value)}
          />

          <select
            value={newEbike.status}
            onChange={(e) => handleChange('status', e.target.value as EbikeStatus)}
            className="border p-2 rounded"
          >

            <option value="">Select Status</option>
            {ebikeStatusOptions.map((status) => (
              <option key={status} value={status}>
                {status}
              </option>
            ))}
          </select>

          <Input
            placeholder="Current Location"
            value={newEbike.currentLocation}
            onChange={(e) => handleChange('currentLocation', e.target.value)}
          />
          <Input
            type="date"
            value={newEbike.lastMaintained?.toDate().toISOString().split('T')[0] || ''}
            onChange={handleDateChange}
          />
          <Input
            type="number"
            placeholder="Battery Capacity (Wh)"
            value={newEbike.batteryCapacity || ''}
            onChange={(e) => handleChange('batteryCapacity', e.target.valueAsNumber || 0)}
          />
          <Input
            type="number"
            placeholder="Range (km)"
            value={newEbike.range || ''}
            onChange={(e) => handleChange('range', e.target.valueAsNumber || 0)}
          />
          <Input
            type="number"
            placeholder="Price Per Day ($)"
            value={newEbike.pricePerDay || ''}
            onChange={(e) => handleChange('pricePerDay', e.target.valueAsNumber || 0)}
          />
        </div>

        <div className="text-right mt-6">
          <Button onClick={saveEbike}>
            {isUpdateMode ? 'Save Ebike' : 'Add Ebike'}
          </Button>
        </div>
      </div>

      <NotificationDialog
        open={dialog.open}
        type={dialog.type}
        title={dialog.title}
        description={dialog.description}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
      />
    </>
  );
}
