'use client';

import { Input } from '@/src/components/ui/input';
import { Textarea } from '@/src/components/ui/textarea';
import { Button } from '@/src/components/ui/button';
import { Checkbox } from '@/src/components/ui/checkbox';
import { Label } from '@/src/components/ui/label';
import { db } from '@/src/firebaseConfig';
import { addDoc, collection, doc, getDocs, updateDoc } from 'firebase/firestore';
import { useCurrentCompanyId } from '@/src/hooks/useCurrentCompanyId';
import { EbikeModel } from '@/src/lib/ebikes/ebikeModelTypes';

type Props = {
  isUpdateModeModel: boolean;
  setIsUpdateModeModel: (v: boolean) => void;
  models: EbikeModel[];
  setModels: (models: EbikeModel[]) => void;
  newEbikeModel: EbikeModel;
  setNewEbikeModel: (model: EbikeModel) => void;
  onSaveComplete?: () => void;
};

export default function EbikeModelForm({
  isUpdateModeModel,
  setIsUpdateModeModel,
  setModels,
  newEbikeModel,
  setNewEbikeModel,
  onSaveComplete,
}: Props) {
  const { companyId } = useCurrentCompanyId();

  const handleChange = <K extends keyof EbikeModel>(field: K, value: EbikeModel[K]) => {
    setNewEbikeModel({
      ...newEbikeModel,
      [field]: value,
    });
  };
  
  

  const resetForm = () => {
    setNewEbikeModel({
      id: '',
      companyId: companyId || '',
      name: '',
      description: '',
      batteryCapacity: 0,
      motorPower: 0,
      topSpeed: 0,
      range: 0,
      weight: 0,
      pricePerDay: 0,
      available: true,
    });
    setIsUpdateModeModel(false);
  };

  const handleSave = async () => {
    if (!companyId) return alert('‚ùå No company linked.');
    if (!newEbikeModel.name || !newEbikeModel.description) return alert('Please fill out required fields.');

    const data = {
      ...newEbikeModel,
      companyId,
      batteryCapacity: Number(newEbikeModel.batteryCapacity),
      motorPower: Number(newEbikeModel.motorPower),
      topSpeed: Number(newEbikeModel.topSpeed),
      range: Number(newEbikeModel.range),
      weight: Number(newEbikeModel.weight),
      pricePerDay: Number(newEbikeModel.pricePerDay),
    };

    if (isUpdateModeModel && newEbikeModel.id) {
      await updateDoc(doc(db, 'ebikeModels', newEbikeModel.id), data);
    } else {
      await addDoc(collection(db, 'ebikeModels'), data);
    }

    resetForm();

    const snapshot = await getDocs(collection(db, 'ebikeModels'));
    const list = snapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    })) as EbikeModel[];
    setModels(list);

    if (onSaveComplete) onSaveComplete();
  };

  return (
    <div className="bg-white rounded-xl shadow p-6 mt-6">
      <h2 className="text-xl font-semibold mb-4">
        {isUpdateModeModel ? 'Edit Ebike Model' : 'Add New Ebike Model'}
      </h2>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Input
          placeholder="Model Name"
          value={newEbikeModel.name}
          onChange={(e) => handleChange('name', e.target.value)}
        />
        <Textarea
          placeholder="Description"
          value={newEbikeModel.description}
          onChange={(e) => handleChange('description', e.target.value)}
        />
        <Input
          type="number"
          placeholder="Battery Capacity (Wh)"
          value={newEbikeModel.batteryCapacity || ''}
          onChange={(e) => handleChange('batteryCapacity', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Motor Power (W)"
          value={newEbikeModel.motorPower || ''}
          onChange={(e) => handleChange('motorPower', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Top Speed (km/h)"
          value={newEbikeModel.topSpeed || ''}
          onChange={(e) => handleChange('topSpeed', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Range (km)"
          value={newEbikeModel.range || ''}
          onChange={(e) => handleChange('range', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Weight (kg)"
          value={newEbikeModel.weight || ''}
          onChange={(e) => handleChange('weight', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Price Per Day ($)"
          value={newEbikeModel.pricePerDay || ''}
          onChange={(e) => handleChange('pricePerDay', e.target.valueAsNumber || 0)}
        />
        <div className="flex items-center space-x-2">
          <Checkbox
            checked={newEbikeModel.available}
            onCheckedChange={(checked) => handleChange('available', !!checked)}
          />
          <Label>Available for rent?</Label>
        </div>
      </div>
      <Button onClick={handleSave} className="mt-6">
        {isUpdateModeModel ? 'Save Changes' : 'Add Model'}
      </Button>
    </div>
  );
}
