'use client';

import { useState } from 'react';
import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import UserTopMenu from '@/src/components/landingpage/UserTopMenu';
import EbikeModelForm from '@/src/components/ebikes/EbikeModelForm';
import EbikeModelTable from '@/src/components/ebikes/EbikeModelTable';
import EbikeForm from '@/src/components/ebikes/EbikeForm';
import EbikeTable from '@/src/components/ebikes/EbikeTable';
import EbikeSearchImportExport from '@/src/components/ebikes/EbikeSearchImportExport';
import PrintQRModal from '@/src/components/ebikes/PrintQRModal';
import NotificationDialog from '@/src/components/ui/NotificationDialog';
import { Ebike } from '@/src/lib/ebikes/ebikeTypes';
import { EbikeModel } from '@/src/lib/ebikes/ebikeModelTypes';
import { useEbikeData } from '@/src/hooks/useEbikeData';
import { useCurrentCompanyId } from '@/src/hooks/useCurrentCompanyId';
import { Timestamp } from 'firebase/firestore';

const emptyEbike: Ebike = {
  id: '',
  modelId: '',
  serialNumber: '',
  vehicleID: '',
  plateNumber: '',
  odo: 0,
  color: '',
  status: 'Available',
  currentLocation: '',
  lastMaintained: Timestamp.fromDate(new Date()),
  batteryCapacity: 0,
  range: 0,
  pricePerDay: 0,
  companyId: '',
  stationId: '',
};

export default function EbikeManagementPage() {
  const { companyId, loading } = useCurrentCompanyId();
  const { ebikes, setEbikes, ebikeModels, setEbikeModels } = useEbikeData(companyId);

  const [newEbikeModel, setNewEbikeModel] = useState<EbikeModel>({
    id: '',
    companyId: companyId || '',
    name: '',
    description: '',
    batteryCapacity: 0,
    motorPower: 0,
    topSpeed: 0,
    range: 0,
    weight: 0,
    pricePerDay: 0,
    available: true,
  });

  const [isUpdateModeModel, setIsUpdateModeModel] = useState(false);
  const [newEbike, setNewEbike] = useState<Ebike>({ ...emptyEbike, companyId: companyId || '' });
  const [isUpdateMode, setIsUpdateMode] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [qrModalOpen, setQrModalOpen] = useState(false);

  const [dialog, setDialog] = useState({
    open: false,
    type: 'info' as 'success' | 'error' | 'info' | 'confirm',
    title: '',
    description: '',
    onConfirm: undefined as (() => void) | undefined,
  });

  const showDialog = (type: 'success' | 'error' | 'info', title: string, description = '') => {
    setDialog({ open: true, type, title, description, onConfirm: undefined });
  };

  const handleModelSaved = () => {
    showDialog('success', isUpdateModeModel ? 'Model updated!' : 'Model added!');
    setIsUpdateModeModel(false);
  };

  const handleEbikeSaved = () => {
    showDialog('success', isUpdateMode ? 'Ebike updated!' : 'Ebike added!');
    setIsUpdateMode(false);
  };

  const handleModelEdit = (model: EbikeModel) => {
    setNewEbikeModel(model);
    setIsUpdateModeModel(true);
  };

  const handleEbikeEdit = (bike: Ebike) => {
    const safeBike = {
      ...bike,
      lastMaintained:
        bike.lastMaintained instanceof Timestamp
          ? bike.lastMaintained
          : Timestamp.fromDate(new Date()),
    };
    setNewEbike(safeBike);
    setIsUpdateMode(true);
  };

  const filteredEbikes = ebikes.filter((bike) => {
    const term = searchTerm.toLowerCase();
    return (
      bike.serialNumber?.toLowerCase().includes(term) ||
      bike.vehicleID?.toLowerCase().includes(term) ||
      bike.plateNumber?.toLowerCase().includes(term) ||
      bike.status?.toLowerCase().includes(term) ||
      bike.currentLocation?.toLowerCase().includes(term)
    );
  });

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <UserTopMenu />

      <main className="p-6 mt-1 flex-grow">
        <h1 className="text-2xl font-semibold mb-4 border-b-2 pb-2">Ebike Management</h1>

        <EbikeModelForm
          isUpdateModeModel={isUpdateModeModel}
          setIsUpdateModeModel={setIsUpdateModeModel}
          models={ebikeModels}
          setModels={setEbikeModels}
          newEbikeModel={newEbikeModel}
          setNewEbikeModel={setNewEbikeModel}
          onSaveComplete={handleModelSaved}
          companyId={companyId || ''}
        />

        <EbikeModelTable
          models={ebikeModels}
          setModels={setEbikeModels}
          onEdit={handleModelEdit}
        />

        <EbikeForm
          newEbike={newEbike}
          setNewEbike={setNewEbike}
          models={ebikeModels}
          isUpdateMode={isUpdateMode}
          setIsUpdateMode={setIsUpdateMode}
          setEbikes={setEbikes}
          onSaveComplete={handleEbikeSaved}
          companyId={companyId || ''}
        />

        <EbikeSearchImportExport
          ebikes={ebikes}
          models={ebikeModels}
          setEbikes={setEbikes}
          searchTerm={searchTerm}
          setSearchTerm={setSearchTerm}
          onPrintAll={() => setQrModalOpen(true)}
        />

        <EbikeTable
          ebikes={filteredEbikes}
          models={ebikeModels}
          setEbikes={setEbikes}
          onEdit={handleEbikeEdit}
        />
      </main>

      <PrintQRModal
        open={qrModalOpen}
        onClose={() => setQrModalOpen(false)}
        ebikes={ebikes}
        models={ebikeModels}
      />

      <Footer />

      <NotificationDialog
        open={dialog.open}
        type={dialog.type}
        title={dialog.title}
        description={dialog.description}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
        onConfirm={dialog.onConfirm}
      />
    </div>
  );
}
