'use client';

import { useState } from 'react';
import { EbikeModel } from '@/src/lib/ebikes/ebikeModelTypes';
import { deleteDoc, doc, collection, getDocs } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import NotificationDialog from '@/src/components/ui/NotificationDialog';
import { Button } from '@/src/components/ui/button';

interface Props {
  models: EbikeModel[];
  setModels: (models: EbikeModel[]) => void;
  onEdit: (model: EbikeModel) => void;
}

export default function EbikeModelTable({ models, setModels, onEdit }: Props) {
  const [dialog, setDialog] = useState<{
    open: boolean;
    title: string;
    onConfirm?: () => void;
  }>({
    open: false,
    title: '',
    onConfirm: undefined,
  });

  const confirmDelete = (id: string) => {
    setDialog({
      open: true,
      title: 'Delete this Ebike Model?',
      onConfirm: async () => {
        try {
          await deleteDoc(doc(db, 'ebikeModels', id));
          const snapshot = await getDocs(collection(db, 'ebikeModels'));
          const updated = snapshot.docs.map((doc) => ({
            id: doc.id,
            ...doc.data(),
          })) as EbikeModel[];
          setModels(updated);
        } catch (err) {
          console.error('‚ùå Failed to delete ebike model', err);
        } finally {
          setDialog({ open: false, title: '' });
        }
      },
    });
  };

  return (
    <div className="bg-white p-6 rounded-xl shadow overflow-x-auto mt-6">
      <h2 className="text-xl font-semibold mb-4">Ebike Models</h2>

      <table className="min-w-full text-sm border border-gray-200">
        <thead className="bg-gray-100 text-gray-700">
          <tr>
            <th className="px-3 py-2 border">Model Name</th>
            <th className="px-3 py-2 border">Description</th>
            <th className="px-3 py-2 border">Battery</th>
            <th className="px-3 py-2 border">Motor</th>
            <th className="px-3 py-2 border">Top Speed</th>
            <th className="px-3 py-2 border">Range</th>
            <th className="px-3 py-2 border">Weight</th>
            <th className="px-3 py-2 border">$/Day</th>
            <th className="px-3 py-2 border">Available</th>
            <th className="px-3 py-2 border">Actions</th>
          </tr>
        </thead>
        <tbody>
          {models.map((model) => (
            <tr key={model.id} className="hover:bg-gray-50">
              <td className="px-3 py-2 border font-medium text-gray-800 whitespace-nowrap">{model.name}</td>
              <td className="px-3 py-2 border text-gray-600">{model.description}</td>
              <td className="px-3 py-2 border whitespace-nowrap">{model.batteryCapacity} Wh</td>
              <td className="px-3 py-2 border whitespace-nowrap">{model.motorPower} W</td>
              <td className="px-3 py-2 border whitespace-nowrap">{model.topSpeed} km/h</td>
              <td className="px-3 py-2 border whitespace-nowrap">{model.range} km</td>
              <td className="px-3 py-2 border whitespace-nowrap">{model.weight} kg</td>
              <td className="px-3 py-2 border whitespace-nowrap">${model.pricePerDay}</td>
              <td className="px-3 py-2 border whitespace-nowrap">
                <span
                  className={`text-xs font-semibold px-2 py-1 rounded ${
                    model.available ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                  }`}
                >
                  {model.available ? 'Yes' : 'No'}
                </span>
              </td>
              <td className="px-3 py-2 border whitespace-nowrap flex gap-1">
                <Button variant="danger" onClick={() => confirmDelete(model.id)}>
                  Delete
                </Button>
                <Button variant="default" onClick={() => onEdit(model)}>
                  Edit
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <NotificationDialog
        open={dialog.open}
        title={dialog.title}
        type="confirm"
        onConfirm={dialog.onConfirm}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
      />
    </div>
  );
}
