'use client';

import { useState, useEffect } from 'react';
import QRCode from 'react-qr-code';
import { Ebike } from '@/src/lib/ebikes/ebikeTypes';
import { EbikeModel } from '@/src/lib/ebikes/ebikeModelTypes';
import { deleteDoc, doc, getDocs, collection, query, where } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { printSingleEbikeQR } from './PrintSingleQR';
import NotificationDialog from '@/src/components/ui/NotificationDialog';
import { Button } from '@/src/components/ui/button';

const statusColors: Record<string, string> = {
  Available: 'bg-green-100 text-green-800',
  'In Use': 'bg-yellow-100 text-yellow-800',
  'Under Maintenance': 'bg-orange-100 text-orange-800',
  Reserved: 'bg-blue-100 text-blue-800',
  Broken: 'bg-red-100 text-red-800',
};

type Props = {
  ebikes: Ebike[];
  models: EbikeModel[];
  onEdit: (ebike: Ebike) => void;
  setEbikes: (ebikes: Ebike[]) => void;
  companyId: string;
};

export default function EbikeTable({ ebikes, models, onEdit, setEbikes, companyId }: Props) {
  const [dialog, setDialog] = useState<{
    open: boolean;
    title: string;
    onConfirm?: () => void;
  }>({ open: false, title: '', onConfirm: undefined });

  useEffect(() => {
    const fetchEbikeByCompany = async () => {
      const q = query(collection(db, 'ebikes'), where('companyId', '==', companyId));
      const snapshot = await getDocs(q);
      const list = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() })) as Ebike[];
      setEbikes(list);
    };

    if (companyId) fetchEbikeByCompany();
  }, [companyId, setEbikes]);

  const handleDelete = (id: string) => {
    setDialog({
      open: true,
      title: 'Delete this ebike?',
      onConfirm: async () => {
        try {
          await deleteDoc(doc(db, 'ebikes', id));
          const q = query(collection(db, 'ebikes'), where('companyId', '==', companyId));
          const snapshot = await getDocs(q);
          const updated = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() })) as Ebike[];
          setEbikes(updated);
        } catch (error) {
          console.error('‚ùå Failed to delete ebike', error);
        } finally {
          setDialog({ open: false, title: '' });
        }
      },
    });
  };

  return (
    <div className="bg-white p-6 rounded-xl shadow mt-6 overflow-x-auto">
      <h2 className="text-xl font-semibold mb-4">Ebike List</h2>

      <table className="min-w-full text-sm border border-gray-200">
        <thead className="bg-gray-100 text-gray-700">
          <tr>
            {[
              'Model',
              'Serial #',
              'Vehicle ID',
              'QR',
              'Plate',
              'Odometer',
              'Color',
              'Status',
              'Location',
              'Battery',
              'Range',
              '$/Day',
              'Last Maintained',
              'Actions',
            ].map((col) => (
              <th key={col} className="border px-3 py-2">
                {col}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {ebikes.map((ebike) => {
            const model = models.find((m) => m.id === ebike.modelId);
            return (
              <tr key={ebike.id} className="hover:bg-gray-50">
                <td className="border px-3 py-2">{model?.name || 'Unknown'}</td>
                <td className="border px-3 py-2">{ebike.serialNumber}</td>
                <td className="border px-3 py-2 font-mono text-xs">{ebike.vehicleID}</td>
                <td className="border px-3 py-2">
                  {ebike.vehicleID ? (
                    <div className="bg-white p-1 rounded w-fit border">
                      <QRCode value={ebike.vehicleID} size={64} />
                    </div>
                  ) : (
                    <span className="text-gray-400 italic">No ID</span>
                  )}
                </td>
                <td className="border px-3 py-2">{ebike.plateNumber}</td>
                <td className="border px-3 py-2">{ebike.odo}</td>
                <td className="border px-3 py-2">{ebike.color}</td>
                <td className="border px-3 py-2">
                  <span
                    className={`text-xs font-semibold px-2 py-1 rounded ${
                      statusColors[ebike.status] || 'bg-gray-200 text-gray-800'
                    }`}
                  >
                    {ebike.status || 'N/A'}
                  </span>
                </td>
                <td className="border px-3 py-2">{ebike.currentLocation}</td>
                <td className="border px-3 py-2">{ebike.batteryCapacity} Wh</td>
                <td className="border px-3 py-2">{ebike.range} km</td>
                <td className="border px-3 py-2">${ebike.pricePerDay}</td>
                <td className="border px-3 py-2">
                  {ebike.lastMaintained?.toDate?.()
                    ? ebike.lastMaintained.toDate().toLocaleDateString('vi-VN')
                    : 'N/A'}
                </td>
                <td className="border px-3 py-2 space-y-1 flex flex-col">
                  <Button variant="danger" onClick={() => handleDelete(ebike.id)}>
                    Delete
                  </Button>
                  <Button variant="outline" onClick={() => onEdit(ebike)}>
                    Edit
                  </Button>
                  <Button
                    variant="default"
                    onClick={() => printSingleEbikeQR(ebike, model?.name || 'Unknown')}
                  >
                    Print
                  </Button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>

      <NotificationDialog
        open={dialog.open}
        title={dialog.title}
        type="confirm"
        onConfirm={dialog.onConfirm}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
      />
    </div>
  );
}
