'use client';

import { useState, useRef } from 'react';
import { Ebike } from '@/src/lib/ebikes/ebikeTypes';
import { EbikeModel } from '@/src/lib/ebikes/ebikeModelTypes';
import { exportEbikesToExcel } from '@/src/lib/ebikes/exportEbikes';
import { importEbikes } from '@/src/lib/ebikes/importEbikes';
import { searchEbikes } from '@/src/lib/ebikes/searchEbikes';
import { deleteAllEbikes } from '@/src/lib/ebikes/deleteEbikes';
import {
  Dialog,
  DialogTrigger,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from '@/src/components/ui/dialog';
import { Button } from '@/src/components/ui/button';
import { Input } from '@/src/components/ui/input';
import NotificationDialog from '@/src/components/ui/NotificationDialog';

interface Props {
  ebikes: Ebike[];
  models: EbikeModel[];
  setEbikes: (ebikes: Ebike[]) => void;
  searchTerm: string;
  setSearchTerm: (term: string) => void;
  onPrintAll: () => void;
  companyId: string; // ‚úÖ Th√™m d√≤ng n√†y
}

export default function EbikeSearchImportExport({
  ebikes,
  models,
  setEbikes,
  searchTerm,
  setSearchTerm,
  onPrintAll,
  companyId, // ‚úÖ Nh·∫≠n t·ª´ props
}: Props) {
  const [openExportDialog, setOpenExportDialog] = useState(false);
  const [openImportDialog, setOpenImportDialog] = useState(false);
  const [openDeleteDialog, setOpenDeleteDialog] = useState(false);
  const [importFile, setImportFile] = useState<File | null>(null);
  const [deleting, setDeleting] = useState(false);
  const [notify, setNotify] = useState({ open: false, type: 'info', title: '', description: '' });

  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const filteredEbikes = searchEbikes(ebikes, searchTerm);

  const showNotify = (type: 'success' | 'error' | 'info', title: string, description = '') => {
    setNotify({ open: true, type, title, description });
  };

  const handleImportConfirm = () => {
    if (importFile) {
      importEbikes(importFile, models, setEbikes);
      setImportFile(null);
      showNotify('success', 'Import completed', 'eBikes imported successfully.');
    }
    setOpenImportDialog(false);
  };

  const handleDeleteAll = async () => {
    setDeleting(true);
    const count = await deleteAllEbikes();
    setEbikes([]);
    setDeleting(false);
    setOpenDeleteDialog(false);
    showNotify('success', 'Deleted All', `${count} eBikes deleted successfully.`);
  };

  return (
    <>
      <div className="flex flex-col md:flex-row justify-between items-stretch md:items-center gap-4 mb-4 mt-4">
        <Input
          placeholder="Search ebikes by Serial, Vehicle ID, Plate Number, Status, Location..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full max-w-md"
        />

        <div className="grid grid-cols-2 sm:flex sm:flex-wrap gap-2 w-full sm:w-auto">
          <Button variant="outline" onClick={() => fileInputRef.current?.click()} className="w-full sm:w-auto">
            Import
          </Button>
          <input
            type="file"
            accept=".xlsx,.xls"
            ref={fileInputRef}
            onChange={(e) => {
              const file = e.target.files?.[0];
              if (file) {
                setImportFile(file);
                setOpenImportDialog(true);
              }
            }}
            className="hidden"
          />

          <Button onClick={() => setOpenExportDialog(true)} className="w-full sm:w-auto">
            Export
          </Button>

          <Button variant="default" onClick={onPrintAll} className="w-full sm:w-auto">
            Print QR Labels
          </Button>

          <Dialog open={openDeleteDialog} onOpenChange={setOpenDeleteDialog}>
            <DialogTrigger asChild>
              <Button variant="danger" className="w-full sm:w-auto col-span-2 sm:col-span-1">
                Delete All
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-md">
              <DialogHeader>
                <DialogTitle className="text-red-600">Confirm Deletion</DialogTitle>
                <p className="text-sm text-muted-foreground mt-2">
                  Are you sure you want to permanently delete <strong>{ebikes.length}</strong> eBikes?
                  <br />
                  This action <span className="font-medium text-red-500">cannot be undone</span>.
                </p>
              </DialogHeader>
              <DialogFooter className="mt-4">
                <Button variant="outline" onClick={() => setOpenDeleteDialog(false)}>Cancel</Button>
                <Button variant="danger" onClick={handleDeleteAll} disabled={deleting}>
                  {deleting ? 'Deleting...' : 'Yes, Delete All'}
                </Button>
              </DialogFooter>
            </DialogContent>
          </Dialog>
        </div>
      </div>

      <Dialog open={openExportDialog} onOpenChange={setOpenExportDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Export Confirmation</DialogTitle>
          </DialogHeader>
          <p>You are about to export <strong>{filteredEbikes.length}</strong> ebikes to Excel.</p>
          <DialogFooter>
            <Button variant="ghost" onClick={() => setOpenExportDialog(false)}>Cancel</Button>
            <Button
              variant="default"
              onClick={() => {
                exportEbikesToExcel(filteredEbikes, models);
                setOpenExportDialog(false);
                showNotify('success', 'Exported', `${filteredEbikes.length} eBikes exported successfully.`);
              }}
            >
              Confirm Export
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      <Dialog open={openImportDialog} onOpenChange={setOpenImportDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Import Confirmation</DialogTitle>
          </DialogHeader>
          <p>Are you sure you want to import ebikes from the selected file?</p>
          <DialogFooter>
            <Button variant="ghost" onClick={() => setOpenImportDialog(false)}>Cancel</Button>
            <Button variant="default" onClick={handleImportConfirm}>Confirm Import</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      <NotificationDialog
        open={notify.open}
        type={notify.type as 'success' | 'error' | 'info'}
        title={notify.title}
        description={notify.description}
        onClose={() => setNotify((prev) => ({ ...prev, open: false }))}
      />
    </>
  );
}